import { MockClient } from '@adsk/fusion-hostapi';
import registerTests from '@adsk/fusion-hostapi/src/it/Client.ittest';
import { runItSelected } from '@adsk/fusion-hostapi/src/it/ITTestRunner';
import qs from 'qs';
import '../../../config/fonts/font.css';

const MOCK_PARAM = 'mode';
const SELECTED_TESTS_PARAM = 'tests';

/*
 * Util for Host App
 */
export class AppUtil {
    /**
     * Util to initial App properly
     * @param appRender render function which returns React.ReactNode
     */
    public static initHostApp(appRender: Function, onMockDataProvider: Function) {
        const rootElement = document.createElement('div');
        document.body.appendChild(rootElement);
        // mock sdkClient if mock switch is on
        const mode: string = qs.parse(window.location.search, { ignoreQueryPrefix: true })[MOCK_PARAM] || '';
        let client;
        // mockData used when debug directly from browser
        if (mode === 'mockData') {
            client = new MockClient();
            onMockDataProvider(client);
        } else if (mode === 'unitTest') {
            // unitTest used when running fremont unit test.
            onMockDataProvider();
        }
        // QT webbrowser injects neutronJavascriptObject at DOMContentLoaded event,
        // we put host app render in window onload event to make sure neutronJavascriptObject is ready before calling it.
        window.onload = () => {
            const waitedTime = 0;
            this.renderAppWithCheck(mode, () => appRender(rootElement, client), waitedTime);
        };
    }

    /**
     * Util to initial interface testing process properly after neutron object is available
     */

    //tslint:disable-next-line:no-single-line-block-comment
    /* istanbul ignore next */
    public static initInterfaceTesting() {
        const queryTests: string = qs.parse(window.location.search, { ignoreQueryPrefix: true })[SELECTED_TESTS_PARAM] || '';
        const selectedTests: string[] = (queryTests || '').split(',');

        window.onload = () => {
            registerTests();
            this.renderAppWithCheck('itTest', () => runItSelected(selectedTests), 0);
        };
    }

    /**
     * Render app with check and timeout
     */
    private static renderAppWithCheck(mode: string, main: Function, waitedTime: number) {
        if (mode === 'mockData') {
            // appRender(rootElement, client);
            main();
            return;
        }

        if (window.neutronJavaScriptObject) {
            //tslint:disable-next-line:no-single-line-block-comment
            /* istanbul ignore if */
            if (waitedTime > 0) {
                console.info(`neutronJavascriptObject is ready after ${waitedTime} miliseconds!`);
            }
            main();
            return;
        }

        // If run unitTest from Fremont, reserve time as the injected js might be not ready.
        // For unitTest, wait 5 seconds at top, othersize 2 seconds
        let isKeepWaiting = true;
        //tslint:disable-next-line:no-single-line-block-comment
        /* istanbul ignore if */
        if (mode === 'unitTest') {
            if (waitedTime >= 5000) {
                isKeepWaiting = false;
            }
        } else {
            if (waitedTime >= 2000) {
                isKeepWaiting = false;
            }
        }

        if (!isKeepWaiting) {
            console.error(`neutronJavascriptObject still not ready after ${waitedTime} miliseconds!`);
            main();
            return;
        }

        const intervalWaitTime: number = 1000; // miliseconds
        setTimeout(() => {
            waitedTime = waitedTime + intervalWaitTime;
            this.renderAppWithCheck(mode, main, waitedTime);
        }, intervalWaitTime);
    }
}
