import { Client } from '../../';

export interface ITTestResult {
    status: boolean;
}

export interface ITTestCase {
    name: string;

    run(hostSdk: Client): Promise<ITTestResult>;
}

/**
 * Interface test runner, used for integration interface testing.
 */
export class ITTestRunner {
    protected hostSdk: Client;
    private readonly testCases: ITTestCase[];
    constructor() {
        this.testCases = [];
        this.hostSdk = new Client();
    }

    public collectTests(selectedTests: string[]): ITTestCase[] {
        selectedTests = selectedTests || [];
        // filter the tests selected
        return (this.testCases || []).filter((element) => {
            return selectedTests.find((selectedName) => {
                return element.name.startsWith(selectedName);
            });
        });
    }

    public allTests(): ITTestCase[] {
        return this.testCases;
    }

    public collect(testCase: ITTestCase) {
        this.testCases.push(testCase);
    }

    public async runTest(selectedTests: string[]) {
        const tests: ITTestCase[] = this.collectTests(selectedTests);

        const testResults: any = {};
        for (const itTest of tests) {
            console.log('Running test: ', itTest.name);
            try {
                testResults[itTest.name] = await itTest.run(this.hostSdk);
            } catch (e) {
                console.log(`error happened when running test ${itTest.name}`, e);
            }
        }
        try {
            await this.hostSdk.testFinished(testResults).then(() => {});
        } catch (error) {
            console.log('error happened when report test result: ', error);
        }
    }
}

const itRunner = new ITTestRunner();
export function interfaceTest(name: string, testFn: (hostSdk: Client) => Promise<ITTestResult>) {
    const testCase: ITTestCase = {
        name: name,
        run: testFn
    };
    itRunner.collect(testCase);
}

/**
 *  Run all selected interface tests
 * @param selectedTests the specified array of tests the invoker want to run.
 */
export async function runItSelected(selectedTests: string[]) {
    await itRunner.runTest(selectedTests);
}

export function listAllTests() {
    return itRunner.allTests();
}
