import { MockProductData } from './ProductTestData';
import { AppType, CommandTypes, ICDEConfig, INeutronJavascriptObject, ITeamData, ITeamItem, MockData } from './types';

/**
 * This is a class which wrapped neutronJavaScriptObject and invoke C++ functions
 */
export default class MockNeutronJavascriptObject implements INeutronJavascriptObject {
    private mockData: MockData;

    constructor() {
        this.mockData = new MockData();
        this.initMockData();
    }

    /**
     * Add mock data for test use
     * @param key input key used by executeQuery/executeCommand
     * @param data mock return data for specified key
     */
    public addMockData(key: string, data: any): void {
        this.mockData[key] = data;
    }

    /**
     * Add mock CDE Config
     * @param data mock return config data for getCDEConfig
     */
    public addMockCDEConfig(data: any): void {
        this.mockData[CommandTypes.GET_CDE_CONFIG] = data;
    }

    /**
     * Execute a command on Fremont side asynchronously and return a promise
     *
     * @param commandType command type maps to C++
     * @param param payload for the inGET_BEAR_TOKEN
     * @returns A promise
     */
    public executeQuery(commandType: CommandTypes | string, param?: object): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            if (!this.mockData.hasOwnProperty(commandType)) {
                reject('exectute failed');
                return;
            }
            const data: any = this.mockData[commandType];
            resolve(JSON.stringify(data));
        });
    }

    /**
     * Execute a command on Fremont side synchronously that has no return value
     *
     * @param commandType command type maps to C++
     * @param param payload for the invoking
     * @returns whether execution succeeded or not
     */
    public executeCommand(commandType: CommandTypes | string, param?: object): boolean {
        if (!this.mockData.hasOwnProperty(commandType)) {
            return false;
        }
        const data: any = this.mockData[commandType];
        if ((data as boolean) === true) {
            return true;
        } else {
            return false;
        }
    }

    private initMockData(): void {
        this.addMockData(CommandTypes.OPEN_DOCUMENT, true);
        this.addMockData(CommandTypes.CHOOSE_COMMIT, { status: true });
        this.addMockData(CommandTypes.OPEN_HISTORY_COMMIT, 'mock success');
        this.addMockData(CommandTypes.GET_BEAR_TOKEN, 'mock bear token');
        this.addMockData(CommandTypes.HELLO_WORLD, 'mock hello');
        this.addMockData(CommandTypes.PRODUCT_NAME, 'mock product name');
        const mockCDEConfig: ICDEConfig = {
            inputData: {
                inputDataId: 'mockInputData',
                sourceAssetUrn: 'urn:adsk.wipqa:dm.lineage:7Z9wCuRlSj6xz5VXXKrqRQ',
                inUseRowKey: 'mockInUseRowKey',
                appType: AppType.HISTORY
            },
            commonConfig: {
                commonConfigId: 'mock commandConfig',
                'ft-endpoint': 'ft-endpoint',
                'symphonydm-endpoint': 'symphonydm-endpoint'
            },
            featureConfig: {
                featureConfigId: 'mock featureConfig',
                'show-create-join-team': 'true'
            }
        };
        this.addMockCDEConfig(mockCDEConfig);
        this.addMockData(CommandTypes.DASHBOARD_CREATE_MILESTONE_COMMAND, true);
        this.addMockData(CommandTypes.SET_SELECTED_COMMIT, { status: true });
        this.addMockData(CommandTypes.CLOSE, true);
        this.addMockData(CommandTypes.COPYTOCLIPBOARD, true);
        this.addMockData(CommandTypes.UPDATE_PRODUCT, true);
        this.addMockData(CommandTypes.UPDATE_PROPERTY, true);
        this.addMockData(CommandTypes.DATA_LOAD_FINISHED, 'mock data load finished');
        this.addMockData(CommandTypes.IT_TEST_FINISHED, 'done');
        this.addMockData(CommandTypes.SEND_ANALYTICS_REPORT, true);
        this.addMockData(CommandTypes.SEND_LOG_TO_FREMENT, true);
        this.addMockTeamData();
        this.addMockData(CommandTypes.SWITCH_TEAM, true);
        this.addMockData(CommandTypes.CREATE_OR_JOIN_TEAM, true);
        this.addMockData(CommandTypes.OPEN_WEB_BROWSER, true);
        this.addMockData(CommandTypes.GET_PROPERTIES, MockProductData);
    }

    private addMockTeamData(): void {
        const mockTeams: ITeamItem[] = [
            {
                name: 'Team 1',
                hubId: 'autodesk001',
                hubUrl: 'url',
                product: 'fusion',
                edition: 'business',
                status: 'Active',
                isDefault: true,
                role: 'ADMIN',
                owner: 'OWNER1'
            },
            {
                name: 'Team 2',
                hubId: 'autodesk002',
                hubUrl: 'url',
                product: 'fusion',
                edition: 'personal',
                status: 'Active',
                isDefault: false,
                role: 'USER',
                owner: 'OWNER2'
            }
        ];

        let mockteamData: ITeamData;
        mockteamData = {
            teamItems: mockTeams,
            currentHubId: 'autodesk001'
        };
        this.addMockData(CommandTypes.GET_TEAM_LIST, mockteamData);
    }
}
