import Aggregator from './src/Aggregator';
import CommandHandlerFactory from './src/CommandHandlerFactory';
import { AGGREGATOR_PREFIX } from './src/Constants';

export { default as Client } from './src/Client';
export { default as MockClient } from './src/MockClient';
export { default as CommandHandlerFactory } from './src/CommandHandlerFactory';
export { default as Notifier } from './src/Notifier';
export { default as Aggregator } from './src/Aggregator';
export * from './src/types';
export { default as MockNeutronJavascriptObject } from './src/MockNeutronJavascriptObject';
export * from './src/Status';
export * from './src/ProductTestData';

declare global {
    // tslint:disable-next-line
    interface Window {
        neutronJavaScriptHandler: any;
    }
}

window.neutronJavaScriptHandler = {
    handle: (cmdId: string, args: any): any => {
        if (cmdId.startsWith(AGGREGATOR_PREFIX)) {
            Aggregator.process(cmdId, args);
            return;
        }
        const handler = CommandHandlerFactory.createHandler(cmdId, args);
        handler && handler.handle();
    }
};
