import { isFunction, isString } from 'lodash';
import { AGGREGATOR_EVENT, AGGREGATOR_QUERY, AGGREGATOR_RESPONSE, NOTIFY_ALL } from './Constants';
import Notifier from './Notifier';
import { INeutronJavascriptObject, WorkflowErrorReason, WorkflowStatus } from './types';

export default class Aggregator {
    private static tickets: { [key: string]: any } = {};
    private static queries: { [key: string]: any } = {};

    public static process(cmdId: string, args: any): void {
        if (cmdId === AGGREGATOR_RESPONSE) {
            this.processResponse(args);
        } else if (cmdId === AGGREGATOR_EVENT) {
            this.processEvent(args);
        } else {
            console.error(`Aggregator event command has wrong format: ${cmdId}`);
        }
    }

    public static async executeQuery(neutron: INeutronJavascriptObject, queryName: string, parameter: any, callback?: any, opt_this?: any) {
        if (!queryName) {
            console.log('queryName is null or is not string');
            return;
        }
        const queryPackage = {
            params: parameter,
            query: queryName
        };
        let queryCallbackStatus = WorkflowStatus.DONE;

        const res = await neutron.executeQuery(AGGREGATOR_QUERY, queryPackage);
        const queryReturnPackage = JSON.parse(res);
        if (queryReturnPackage.status.indexOf(WorkflowStatus.ERROR) !== -1) {
            queryCallbackStatus = WorkflowStatus.ERROR;
            console.log(`queryReturnPackage contains error: ${queryReturnPackage.error_reason}`);
            Notifier.fireErrorMessage(queryReturnPackage.error_reason);
        } else if (queryReturnPackage.status === WorkflowStatus.PROGRESS) {
            queryCallbackStatus = WorkflowStatus.PROGRESS;
            const ticket = queryReturnPackage.ticket;
            ticket && this.storeTicket(ticket, queryName, callback);
        }
        if (callback) {
            callback.call(opt_this, queryReturnPackage.data, queryCallbackStatus);
        } else {
            Notifier.fireBrowserApiEvent(queryName, queryReturnPackage.data, queryCallbackStatus);
        }
    }

    private static storeTicket(ticket: string, queryName: string, callback: any): void {
        callback = callback || NOTIFY_ALL;
        if (!this.tickets[ticket]) {
            this.tickets[ticket] = [callback];
        } else {
            const callbackArray = this.tickets[ticket];
            const containsNotifyAll = callbackArray.includes(NOTIFY_ALL);
            if ((!containsNotifyAll && callbackArray.length > 0 && callback === NOTIFY_ALL) || (containsNotifyAll && callback !== NOTIFY_ALL)) {
                console.log(`Aggregator's callback array for ticket ${ticket} must not be mixed!`);
            } else {
                callbackArray.push(callback);
                this.tickets[ticket] = callbackArray;
            }
        }

        if (this.queries[ticket]) {
            console.warn(`queryMap already contains ticket ${ticket} for query ${queryName}`);
        } else {
            this.queries[ticket] = queryName;
        }
    }

    private static removeTicket(ticket: string): void {
        delete this.queries[ticket];
        delete this.tickets[ticket];
    }

    private static processTicket(ticket: string, data: any, status: string): void {
        if (!this.tickets[ticket]) {
            return;
        }
        const callbackArray = this.tickets[ticket];

        callbackArray.forEach((callback: any) => {
            if (isFunction(callback)) {
                callback(data, status);
            } else if (isString(callback) && callback === NOTIFY_ALL) {
                if (!this.queries[ticket]) {
                    console.log(`Aggregator cannot find query name for ticket ${ticket}`);
                } else {
                    Notifier.fireBrowserApiEvent(this.queries[ticket], data, status);
                }
            }
        });
    }

    private static processResponse(args: any): void {
        const isHtml = (testString: string): boolean => {
            const htmlRegex = new RegExp('<([A-Za-z][A-Za-z0-9]*)\\b[^>]*>(.*?)</\\1>');
            return htmlRegex.test(testString);
        };

        if (!args) {
            console.error('Aggregator response is null');
            return;
        }

        const responsePackage = JSON.parse(args);
        if (!responsePackage.ticket) {
            console.error('AggregatorResponsePackage does not contain ticket.');
            return;
        }
        this.processTicket(responsePackage.ticket, responsePackage.data, responsePackage.status);

        if (responsePackage.status.indexOf(WorkflowStatus.ERROR) !== -1) {
            let errMsg = 'Some error occurs.';
            if (responsePackage.data[WorkflowErrorReason.WORKFLOW_KEY_REASON_ERROR]) {
                if (isHtml(responsePackage.data[WorkflowErrorReason.WORKFLOW_KEY_REASON_ERROR])) {
                    errMsg = 'Operation failed';
                } else {
                    errMsg = responsePackage.data[WorkflowErrorReason.WORKFLOW_KEY_REASON_ERROR];
                    if (errMsg.indexOf('Read access denied') !== -1) {
                        console.warn(errMsg);
                        errMsg = 'Read access denied';
                    }
                }
            }

            Notifier.fireErrorMessage(errMsg);
        }

        if (responsePackage.status !== WorkflowStatus.PROGRESS) {
            this.removeTicket(responsePackage.ticket);
        }
    }

    private static processEvent(args: any): void {
        if (!args) {
            console.error('Aggregator response is null');
            return;
        }
        const eventPackage = JSON.parse(args);
        Notifier.fireBrowserApiEvent(eventPackage.event, eventPackage.data, eventPackage.status);
    }
}
