import * as React from 'react';

import { Mediator } from '@adsk/cde-mediator';
import { SetupSheetConfiguratorApp } from '@adsk/cde-setupsheet-configurator';
import { BaseApp } from '@adsk/fusion-cde-base-app';
import { FusionSetupSheetDataProvider } from '@adsk/fusion-cde-data-providers';

const CDE_PACKAGE_NAME = 'cde-setupsheet-configurator';

/**
 * SetupSheetConfiguratorHostApp - Renders a wrapped CDE Setup Sheet Configurator
 * with Fusion-based decorators and data interactions
 */
class SetupSheetConfiguratorHostApp extends BaseApp {
    constructor(props: any) {
        super(props, new Mediator(), CDE_PACKAGE_NAME);

        // Register the data provider.
        const provider: FusionSetupSheetDataProvider = new FusionSetupSheetDataProvider(this.hostSdk);
        this.mediator.registerDataProvider(provider);
    }

    public render(): React.ReactNode {
        return <SetupSheetConfiguratorApp store={this.mediator.getStore()} />;
    }
}

export { SetupSheetConfiguratorHostApp };
