import {
    DataProviderType,
    DatumTypes,
    IFusionTeamHub,
    ITeamDataProvider,
    TeamEdition,
    TeamHubDatumCollection,
    TeamProduct,
    TeamStatus
} from '@adsk/cde-data-types';
import { Client, ITeamData, ITeamItem } from '@adsk/fusion-hostapi';

export default class FusionTeamDataProvider implements ITeamDataProvider {
    private client: Client;

    constructor(client: Client) {
        this.client = client;
    }

    public getProviderType(): DataProviderType {
        return DataProviderType.TEAM_PROVIDER_TYPE;
    }

    public async getTeamItems(): Promise<TeamHubDatumCollection> {
        const itemsData: ITeamData = await this.client.getTeamList();
        const teamItems: ITeamItem[] = itemsData.teamItems;
        const currentHubId: string = itemsData.currentHubId;
        return this.mapToTeamDatumCollection(teamItems, currentHubId);
    }

    private getTeamDatumFromItem(item: ITeamItem): IFusionTeamHub {
        return {
            name: item.name,
            id: item.hubId,
            hubId: item.hubId,
            detailsUrl: item.hubUrl,
            edition: TeamEdition[item.edition],
            product: TeamProduct[item.product],
            teamStatus: TeamStatus[item.status],
            owner: item.owner,
            role: item.role,
            isDefaultHub: item.isDefault,
            config: {
                isShareFileAllowed: false
            },
            urn: 'URN',
            datumType: DatumTypes.HUBS,
            childIds: [],
            allowedActions: [],
            readOnly: true,
            isRootItem: false
        };
    }

    private mapToTeamDatumCollection(teamItems: ITeamItem[], currentHubId: string): TeamHubDatumCollection {
        const newItems: TeamHubDatumCollection = new TeamHubDatumCollection();
        teamItems.forEach((item) => {
            newItems.add(this.getTeamDatumFromItem(item));
        });
        newItems.currentHubId = currentHubId;
        return newItems;
    }
}
