import { CommandTypes, INeutronJavascriptObject } from './types';

declare global {
    // tslint:disable-next-line
    interface Window {
        neutronJavaScriptObject: any;
    }
}

/**
 * This is a class which wrapped neutronJavaScriptObject and invoke C++ functions
 */
export default class NeutronJavascriptObject implements INeutronJavascriptObject {
    /**
     * Execute a command on Fremont side asynchronously and return a promise
     *
     * @param commandType command type maps to C++
     * @param param payload for the invoking
     * @returns A promise
     */
    public executeQuery(commandType: CommandTypes | string, param?: object): Promise<any> {
        return new Promise<any>((resolve, reject) => {
            const arg = JSON.stringify(param || {});
            if (window && window.neutronJavaScriptObject && window.neutronJavaScriptObject.executeQuery) {
                const neutronContext: any = window.neutronJavaScriptObject.initialContext || {};
                if (neutronContext.isUsingQtWebEngine === true) {
                    window.neutronJavaScriptObject.executeQuery(commandType, arg, (res: any) => {
                        resolve(res);
                    });
                } else {
                    // Do not remove, warning message for debugging purpose
                    console.warn('Using fremont legacy browser...');
                    const res = window.neutronJavaScriptObject.executeQuery(commandType, arg);
                    resolve(res);
                }
            } else {
                // Do not remove, warning message for debugging purpose
                console.error(`NeutronJavascriptObject is not ready yet! command: ${commandType}`);
                reject('exectute failed');
            }
        });
    }

    /**
     * Execute a command on Fremont side synchronously that has no return value
     *
     * @param commandType command type maps to C++
     * @param param payload for the invoking
     * @returns whether execution succeeded or not
     */
    public executeCommand(commandType: CommandTypes | string, param?: object): boolean {
        const arg = JSON.stringify(param || {});
        if (!window || !window.neutronJavaScriptObject) {
            return false;
        }
        window.neutronJavaScriptObject.executeCommand(commandType, arg);
        return true;
    }
}
