import { omit } from 'lodash';
import Aggregator from './Aggregator';
import NeutronJavascriptObject from './NeutronJavascriptObject';
import { default as Notifier } from './Notifier';
import { IStatus } from './Status';
import * as HostTypes from './types';

/**
 * ```typescript
 * const client = new Client();
 * client.hello().then(res => {
 *     console.log(res);
 * }).then(e=>{
 *     console.log(e);
 * })
 * ```
 */
export default class Client implements HostTypes.IAuthorize, HostTypes.ITest, HostTypes.IOperation, HostTypes.IProjectInfo {
    public neutron: HostTypes.INeutronJavascriptObject;

    constructor(neutron?: HostTypes.INeutronJavascriptObject) {
        this.neutron = neutron || new NeutronJavascriptObject();
    }

    /**
     * Config a different neutron object for mock data and testing
     * @param neutron object inherits from INeutronJavascriptObject
     */
    public setNeutronJavascriptObject(neutron: HostTypes.INeutronJavascriptObject): void {
        this.neutron = neutron;
    }

    /**
     * Get using neutronJavascriptObject
     */
    public getNeutronJavascriptObject(): HostTypes.INeutronJavascriptObject {
        return this.neutron;
    }

    /**
     * Use aggreagator to executeQuery, maps to Fremont data panel Aggregator workflows
     * @param queryName queryName
     * @param params parameters
     */
    public executeWorkflow(queryName: string, params: any): Promise<void> {
        return Aggregator.executeQuery(this.neutron, queryName, params);
    }

    public getBearerToken(): Promise<string> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.GET_BEAR_TOKEN);
    }

    public getCDEConfig(): Promise<HostTypes.ICDEConfig> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.GET_CDE_CONFIG).then((res) => {
            const t = JSON.parse(res);
            return {
                inputData: t.inputData,
                commonConfig: t.commonConfig,
                featureConfig: t.featureConfig
            } as HostTypes.ICDEConfig;
        });
    }

    //tslint:disable-next-line
    /* istanbul ignore next */
    // Ignore code coverage as there is no self logic here.
    // Please remove that when adding logic to this function
    public getProduct(params: any): Promise<string> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.GET_PROPERTIES, params);
    }

    //tslint:disable-next-line
    /* istanbul ignore next */
    // Ignore code coverage as there is no self logic here.
    // Please remove that when adding logic to this function
    public updateProduct(params: any): Promise<void> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.UPDATE_PRODUCT, params);
    }

    public executeQuery(commondType: HostTypes.CommandTypes, param: any): Promise<any> {
        return this.neutron.executeQuery(commondType, param);
    }

    public openDocument(param: any): boolean {
        return this.neutron.executeCommand(HostTypes.CommandTypes.OPEN_DOCUMENT, param);
    }

    public openHistoryCommit(param: any): Promise<IStatus> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.OPEN_HISTORY_COMMIT, param).then((response) => {
            return JSON.parse(response);
        });
    }

    public closeDialog(): Promise<any> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.CLOSE).then((res) => {
            if (res === '') return true;
            return JSON.parse(res);
        });
    }

    public hello(): Promise<string> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.HELLO_WORLD).then((res) => {
            return JSON.parse(res) as string;
        });
    }

    public getProductName(): Promise<string> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.PRODUCT_NAME).then((res) => {
            return JSON.parse(res) as string;
        });
    }

    public createMilestoneCmd(extUrn: string, versionUrn: string, name: string): boolean {
        const args = {
            extUrn: extUrn,
            fileUri: versionUrn,
            name: name
        };
        return this.neutron.executeCommand(HostTypes.CommandTypes.DASHBOARD_CREATE_MILESTONE_COMMAND, args);
    }

    public setSelectedCommit(urn: string, isTip: boolean, userActionKey: string, extraInfo: any): Promise<string> {
        const args = {
            fileUrn: urn,
            isTip: isTip,
            userActionKey: userActionKey,
            extraInfo: extraInfo
        };
        return this.neutron.executeQuery(HostTypes.CommandTypes.SET_SELECTED_COMMIT, args).then((res) => {
            return JSON.parse(res) as string;
        });
    }

    public chooseCommit(urn: string, userActionKey: string): Promise<string> {
        const args = {
            fileUrn: urn,
            userActionKey: userActionKey
        };
        return this.neutron.executeQuery(HostTypes.CommandTypes.CHOOSE_COMMIT, args).then((res) => {
            return JSON.parse(res) as string;
        });
    }

    public copyToClipboard(copyData: any): Promise<any> {
        const args = {
            data: copyData
        };
        return this.neutron.executeQuery(HostTypes.CommandTypes.COPYTOCLIPBOARD, args).then((res) => {
            return JSON.parse(res);
        });
    }

    public updateProperty(propName: string, propValue: string): Promise<any> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.UPDATE_PROPERTY, { name: propName, value: propValue }).then((res) => {
            return JSON.parse(res);
        });
    }

    /**
     * Notify Fusion that CDE data load finished (Html contents are ready)
     * Used by automation to get html contents for validation
     */
    public dataLoadFinished(): Promise<any> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.DATA_LOAD_FINISHED).then((res) => {
            if (res === '') return true;
            return JSON.parse(res);
        });
    }

    /**
     *  Notify Fusion that CDE side interface testing is finished
     * @param resultData report the testing data to fusion side.
     */
    public testFinished(resultData: any): Promise<any> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.IT_TEST_FINISHED, resultData).then((res) => {
            if (res === '') return true;
            return JSON.parse(res);
        });
    }

    public sendAnalyticsReport(eventName: string, eventPayload: any): Promise<boolean> {
        const args = {
            commandName: eventName,
            type: eventPayload.cdeComponent,
            accessPoint: eventPayload.cdeComponentAccessPoint,
            attributes: { ...omit(eventPayload, ['cdeComponent', 'cdeComponentAccessPoint']) }
        };
        return this.neutron.executeQuery(HostTypes.CommandTypes.SEND_ANALYTICS_REPORT, args).then((res) => {
            return JSON.parse(res) === true;
        });
    }

    /**
     * send log from CDE components to Frement
     */
    public sendLogToFremont(param: any): Promise<boolean> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.SEND_LOG_TO_FREMENT, param).then((res) => {
            return JSON.parse(res) === true;
        });
    }

    /**
     * Get team hub list from Fremont
     */
    public getTeamList(): Promise<HostTypes.ITeamData> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.GET_TEAM_LIST).then((res) => {
            return JSON.parse(res);
        });
    }

    /**
     * Switch to specified team hub
     * @param hubId team hub Id
     */
    public switchTeam(hubId: string): Promise<boolean> {
        const args = {
            hubId: hubId
        };
        return this.neutron.executeQuery(HostTypes.CommandTypes.SWITCH_TEAM, args).then((res) => {
            if (!res) {
                return true;
            }
            return JSON.parse(res) === true;
        });
    }

    /**
     * Invoke create-or-join team command
     */
    public createOrJoinTeam(): Promise<boolean> {
        return this.neutron.executeQuery(HostTypes.CommandTypes.CREATE_OR_JOIN_TEAM).then((res) => {
            return JSON.parse(res) === true;
        });
    }

    /**
     * Open page in external web browser
     */
    public openWebBrowser(urlToOpen: string, needSignUrl: boolean): Promise<boolean> {
        const args: any = {
            url: urlToOpen,
            needSignUrl: needSignUrl
        };
        return this.neutron.executeQuery(HostTypes.CommandTypes.OPEN_WEB_BROWSER, args).then((res) => {
            return JSON.parse(res) === true;
        });
    }

    public registerCDERefresh(handler: Function): void {
        Notifier.register(HostTypes.AppHostEvent.CDE_REFRESH, {}, (_response) => {
            // console.log(`status: ${_response.status}`);
            handler(_response);
        });
    }

    /**
     * Used to register handler for event when CDE Application been closed by the user.
     * Provide a good place for Application to do clean-up work.
     */
    public registerCDEClose(handler: Function): void {
        Notifier.register(HostTypes.AppHostEvent.CDE_CLOSE, {}, (_response) => {
            handler(_response);
        });
    }
}
