import { DefaultLogger, LogLevel } from '@adsk/cde-data-types';
import { Client } from '@adsk/fusion-hostapi';
/**
 * A trivial logger client
 */
export default class FusionLogger extends DefaultLogger {
    public level: LogLevel;
    private client: Client;
    constructor(level: LogLevel, client: Client) {
        super();
        this.level = level;
        this.client = client;
    }

    public async log(level: LogLevel, message: any): Promise<void> {
        return new Promise<void>((resolve) => {
            //send messge to frement
            const log = {
                message: message,
                level: LogLevel[level]
            };
            // tslint:disable-next-line: no-floating-promises
            this.client
                .sendLogToFremont(log)
                .then(() => {})
                .catch(() => {});
            resolve();
        });
    }
}
