import { ConnectivityHandler, HistoryHandler } from './handlers';
import { ICommandHandler } from './types';

/**
 * This is a factory class to return a proper handler according to command id
 */
export default class CommandHandlerFactory {
    /**
     *
     * @param cmdId command id maps to C++
     * @param args payload from C++
     */
    public static createHandler(cmdId: string, args: any): ICommandHandler | null {
        switch (cmdId) {
            case 'connectivityChanged':
                return new ConnectivityHandler(args);
            case 'historyChanged':
                return new HistoryHandler(args);
            default:
                return null;
        }
    }
}
