import Client from '../Client';
import { interfaceTest, ITTestCase, ITTestResult, listAllTests } from './ITTestRunner';

export default function registerTests() {
    interfaceTest(
        'history.open',
        async (hostSdk: Client): Promise<ITTestResult> => {
            const file = await import('./data.ittest.json');
            console.log(file.byId);
            const rowData = file.byId['urn:adsk.wipqa:fs.file:vf.7Z9wCuRlSj6xz5VXXKrqRQ?version=1'];

            const openRequest = {
                extUrn: rowData.assetUrn,
                fileUri: rowData.urn,
                isTip: rowData.isTip,
                userActionKey: 'InterfaceTest'
            };
            const openHistoryResultStatus: any = await hostSdk.openHistoryCommit(openRequest);
            console.log(openHistoryResultStatus);
            return openHistoryResultStatus;
        }
    );

    interfaceTest(
        'history.setSelected',
        async (hostSdk: Client): Promise<ITTestResult> => {
            const file = await import('./data.ittest.json');
            console.log(file.byId);
            const rowData = file.byId['urn:adsk.wipqa:fs.file:vf.7Z9wCuRlSj6xz5VXXKrqRQ?version=1'];

            const setSelectedResultStatus: any = await hostSdk.setSelectedCommit(rowData.urn, rowData.isTip, 'InterfaceTest', {});
            console.log(setSelectedResultStatus);
            return setSelectedResultStatus;
        }
    );

    interfaceTest(
        'history.chooseCommit',
        async (hostSdk: Client): Promise<ITTestResult> => {
            const file = await import('./data.ittest.json');
            const rowData = file.byId['urn:adsk.wipqa:fs.file:vf.7Z9wCuRlSj6xz5VXXKrqRQ?version=1'];

            const chooseCommitResultStatus: any = await hostSdk.chooseCommit(rowData.urn, 'InterfaceTest');
            console.log(chooseCommitResultStatus);
            return chooseCommitResultStatus;
        }
    );

    const tests: ITTestCase[] = listAllTests();
    (tests || []).forEach((testCase) => {
        console.log(`Found test: ${testCase.name}`);
    });
}
