import {
    DataProviderType,
    DatumTypes,
    HistoryDataSource,
    HistoryDataTypes,
    HistoryDatum,
    HistoryDatumCollection,
    IGetHistoryItemsOptions,
    IHistoryDataProvider
} from '@adsk/cde-data-types';
import { Client, Notifier } from '@adsk/fusion-hostapi';

const GET_VERSIONS_FOR_FILES = 'get_versions_for_files';

export default class FusionHistoryDataProvider implements IHistoryDataProvider {
    private client: Client;

    constructor(client: Client) {
        this.client = client;
    }

    public getProviderType(): DataProviderType {
        return DataProviderType.HISTORY_PROVIDER_TYPE;
    }

    public async getHistoryItems(sourceAssetUrn: string, options: IGetHistoryItemsOptions): Promise<HistoryDatumCollection> {
        return new Promise((resolve, reject) => {
            Notifier.register(GET_VERSIONS_FOR_FILES, {}, (response) => {
                console.log(`status: ${response.status}`);
                if (response.status === 'error') {
                    reject('Get history items error occurred');
                } else if (response.status === 'done') {
                    resolve(this.mapToHistoryDatumCollection(response.data.versions || [], sourceAssetUrn));
                }
            });

            this.client
                .executeWorkflow(GET_VERSIONS_FOR_FILES, { id: sourceAssetUrn })
                .then(() => {})
                .catch(() => {});
        });
    }

    public getAdditionalHistoryItems(sourceAssetUrn: string, nextPageUrl: string): Promise<HistoryDatumCollection> {
        throw new Error('Not Implemented yet.');
    }

    private getHistoryDatumFromVersion(version: any, sourceAssetUrn: string): HistoryDatum {
        return {
            id: version.urn,
            name: '',
            urn: version.urn,
            author: version.displayName,
            createdTime: new Date(version.lastModifiedTime),
            assetUrn: sourceAssetUrn,
            productId: 'productId',
            description: version.description,
            source: HistoryDataSource.WIP,
            dataType: HistoryDataTypes.Version,
            datumType: DatumTypes.FILES,
            allowedActions: ['OPEN', 'CREATE_MILESTONE']
        };
    }

    private getHistoryDatumFromMileStone(version: any, sourceAssetUrn: string): HistoryDatum {
        return {
            id: `${version.urn}:milestone`,
            name: version.milestoneTitle,
            urn: `${version.urn}:milestone`,
            author: version.displayName,
            createdTime: new Date(version.lastModifiedTime),
            assetUrn: sourceAssetUrn,
            productId: 'productId',
            description: version.description,
            source: HistoryDataSource.WIP,
            dataType: HistoryDataTypes.Milestone,
            datumType: DatumTypes.FILES,
            allowedActions: ['OPEN']
        };
    }

    private mapToHistoryDatumCollection(versions: any, sourceAssetUrn: string): HistoryDatumCollection {
        const items: HistoryDatumCollection = new HistoryDatumCollection();
        versions.forEach((version) => {
            items.add(
                version.isMilestone
                    ? this.getHistoryDatumFromMileStone(version, sourceAssetUrn)
                    : this.getHistoryDatumFromVersion(version, sourceAssetUrn)
            );
        });

        return items;
    }
}
