import { IAnalytics, IAnalyticsPayload, LogLevel } from '@adsk/cde-data-types';
import { IMediator, Mediator } from '@adsk/cde-mediator';
import { Client, ComponentAccessPoint, ComponentEventName, ICDEConfig } from '@adsk/fusion-hostapi';
import qs from 'qs';
import * as React from 'react';
import { default as FusionLogger } from './FusionLogger';

const MEDIATOR_PACKAGE_ID = '@adsk/cde-mediator';
const FUSION_TEAM_ENDPOINT = 'ft-endpoint';
const SYMPHONYDM_ENDPOINT = 'symphonydm-endpoint';
const FUSIONDM_ENDPOINT = 'fusiondm-endpoint';
const GET_DATA_FROM_FUSION = 'get-data-from-fusion';
const LOCALE_PARAM = 'locale';

/**
 * Base for CDE Host App, providing common configs and utils
 * Such as: token, endpoints, locale, analytics, etc.
 */
export abstract class BaseApp extends React.Component<any, any> {
    protected hostSdk: Client;
    protected mediator: IMediator;
    protected cdeCompName: string;

    constructor(props: any, mediator: Mediator, cdeCompName: string) {
        super(props);

        this.hostSdk = this.props.hostSdk || new Client();
        this.mediator = mediator;
        this.cdeCompName = cdeCompName;

        this.setAuthFunc = this.setAuthFunc.bind(this);
        this.analytics = this.analytics.bind(this);
        this.onBtnClose = this.onBtnClose.bind(this);

        // tslint:disable-next-line: no-floating-promises
        this.initConfig();
    }

    /**
     * Set locale to CDE component
     */
    public setLauguage() {
        const curLocale: string = qs.parse(window.location.search, { ignoreQueryPrefix: true })[LOCALE_PARAM] || 'en';

        this.mediator.getStore().dispatch(
            this.mediator
                .getComponent('@adsk/cde-mediator')
                .getActions()
                .setLocale({ locale: curLocale })
        );

        this.mediator.getLogger().info(`***************Setting locale string as ${curLocale}`);
    }

    public setAuthFunc(): Promise<any> {
        return new Promise((resolve, reject) => {
            this.hostSdk
                .getBearerToken()
                .then((res: any) => {
                    resolve(res);
                })
                .catch((e: any) => {
                    this.handleError(e);
                    reject(e);
                });
        });
    }

    /**
     * Built-in React lifecycle callback - called after updating occurs
     * This method is not called for the initial render
     * preProps: previous properties
     */
    public componentDidUpdate(prevProps): void {}

    /**
     * Send dataLoadFinished event
     */
    public async dataLoadFinished() {
        try {
            await this.hostSdk.dataLoadFinished().then(() => {});
        } catch (error) {
            this.handleError(error);
        }
    }

    /**
     * Init config, update to mediator
     */
    public async initConfig() {
        this.setLauguage();
        this.mediator.setAnalytics(this.analytics());

        this.mediator.setLogger(new FusionLogger(LogLevel.INFO, this.hostSdk));

        const mediatorActions = this.mediator.getComponent(MEDIATOR_PACKAGE_ID).getActions();
        const store: any = this.mediator.getStore();
        store.dispatch(mediatorActions.setAuthAPI({ func: this.setAuthFunc }));

        try {
            const res = await this.hostSdk.getCDEConfig();
            // console.log(`my config: ${JSON.stringify(res)}`);

            const featureFlags = res.featureConfig || {};
            this.mediator.setFeatureFlags(featureFlags);

            const ft_endpoint: string = res.commonConfig[FUSION_TEAM_ENDPOINT] || '';
            const symphonydm_endpoint: string = res.commonConfig[SYMPHONYDM_ENDPOINT] || '';
            const fusiondm_endpoint: string = res.commonConfig[FUSIONDM_ENDPOINT] || '';

            const is_get_data_from_fusion: string = res.featureConfig[GET_DATA_FROM_FUSION];
            if (is_get_data_from_fusion === 'true') {
                this.onSetFusionDataProvider();
            }

            store.dispatch(mediatorActions.setFTAPIEndPoint(ft_endpoint));
            store.dispatch(mediatorActions.setSymphonyAPIEndPoint(symphonydm_endpoint));
            store.dispatch(mediatorActions.setFusionDMApiEndPoint(fusiondm_endpoint));

            this.onInitConfig(res);
        } catch (error) {
            this.handleInitConfigError(error);
        }
    }

    /**
     * Handle error by logging or update UI
     * @param error error message
     */
    public handleError(error: any): void {
        // TODO: use log module
        console.error(error);
    }

    /**
     * Return analytics object for sending analytics to Fremont
     */
    public analytics(): IAnalytics {
        return {
            track: (eventName: string, eventPayload: IAnalyticsPayload) => {
                this.hostSdk
                    .sendAnalyticsReport(eventName, eventPayload)
                    .then(() => {})
                    .catch(() => {});
            }
        };
    }

    /**
     * Util for analytic events
     * @param eventName analytic event name, define in hostAPI
     * @param accessPoint accessPoint, define in hostAPI
     */
    public trackEvent(eventName: ComponentEventName, accessPoint: ComponentAccessPoint) {
        this.mediator.getAnalytics().track(eventName, {
            cdeComponent: this.cdeCompName,
            cdeComponentAccessPoint: accessPoint
        });
    }

    /**
     * Init config for concrete app to handle
     */
    protected onInitConfig(res: ICDEConfig): void {}

    /**
     * Get Fusion data provider which get data form Fremont
     */
    protected onSetFusionDataProvider(): void {
        console.error('No fusion data provider added!');
    }

    /**
     * Close CDE UI
     * @param e event from UI
     */
    protected onBtnClose(e: any) {
        this.hostSdk
            .closeDialog()
            .then(() => {})
            .catch(() => {});
    }

    /**
     * Handle init config error. Make this specific error handler callback so the subclasses can handle init config error
     * different with other errors.
     * @param error error message
     */
    protected handleInitConfigError(error: any) {
        console.error(error);
    }
}
