import { DataProviderType, ISetupSheetConfigurationOption, ISetupSheetDataProvider } from '@adsk/cde-data-types';
import { Client } from '@adsk/fusion-hostapi';

const GET_SETUP_SHEET_CONFIGURATIONS = 'get_setup_sheet_configurations';
const SAVE_SETUP_SHEET_CONFIGURATION = 'save_setup_sheet_configuration';

/**
 * FusionSetupSheetDataProvider
 * Provides Setup Sheet data (configurations and payloads) from Fusion
 */
class FusionSetupSheetDataProvider implements ISetupSheetDataProvider {
    private client: Client;

    constructor(client: Client) {
        this.client = client;
    }

    /**
     * getSetupSheetContents
     * NOT IMPLEMENTED Contents are passed directly into React component
     * Unnecessary to attempt to gather any contents here
     *
     * @returns empty string
     */
    public async getSetupSheetContents(): Promise<any> {
        return '';
    }

    /**
     * getSetupSheetConfigurations
     * Sends action to Fusion client to retrieve all Setup Sheet configurations
     * from disk and returns them
     *
     * @returns Promise to array of setup sheet configuration options in the form
     */
    public async getSetupSheetConfigurations(): Promise<ISetupSheetConfigurationOption[]> {
        const configStr = await this.client.neutron.executeQuery(GET_SETUP_SHEET_CONFIGURATIONS);
        return JSON.parse(configStr);
    }

    /**
     * saveSetupSheetConfiguration
     * Sends action to Fusion client to save Setup Sheet configuration from disk
     *
     * @returns return file full path when successfull and empty string when unsuccessfull.
     */
    public async saveSetupSheetConfiguration(config: ISetupSheetConfigurationOption): Promise<any> {
        return this.client.neutron.executeQuery(SAVE_SETUP_SHEET_CONFIGURATION, config);
    }

    /**
     * Return the data provider type
     *
     * @returns The data provider type
     */
    public getProviderType(): DataProviderType {
        return DataProviderType.SETUPSHEET_PROVIDER_TYPE;
    }
}

export { FusionSetupSheetDataProvider, GET_SETUP_SHEET_CONFIGURATIONS, SAVE_SETUP_SHEET_CONFIGURATION };
