import CustomEvent from 'custom-event-js';
import { isFunction, isObject } from 'lodash';
import { EventType } from './types';

export default class Notifier {
    public static register(eventType: string, handlerObject: any, handlerFunction: Function, justOnce: boolean = false): void {
        if (!isObject(handlerObject) || isFunction(handlerObject)) {
            console.error("Invalid parameter! It must be 'object'");
            return;
        }

        if (!isFunction(handlerFunction)) {
            console.error('Invalid parameter! It must be some function');
            return;
        }

        CustomEvent.on(eventType, (data) => {
            handlerFunction.call(handlerObject, data.detail);
            justOnce && this.unRegister(eventType);
        });
    }

    public static unRegister(eventType: string): void {
        CustomEvent.off(eventType);
    }

    public static fireBrowserApiEvent(eventType: string, data: any, status: string): void {
        CustomEvent.dispatch(eventType, { data, status });
    }

    public static fireErrorMessage(message: string, showPermanently: boolean = false): void {
        CustomEvent.dispatch(EventType.ERROR_MESSAGE, { message, showPermanently });
    }
}
